type thing = { facebook: type2; pokemon: type2 };

type type2 = {
  like: string;
  love: string;
  haha: string;
  wow: string;
  sad: string;
  angry: string;
};

const icons: thing = {
  facebook: {
    like:
      'data:image/png;base64,',
    love:
      'data:image/png;base64,',
    haha:
      'data:image/png;base64,',
    wow:
      'data:image/png;base64,',
    sad:
      'data:image/png;base64,',
    angry:
      'data:image/png;base64,',
  },
  pokemon: {
    like:
      'data:image/png;base64,',
    love:
      'data:image/png;base64,',
    haha:
      'data:image/png;base64,',
    wow:
      'data:image/png;base64,',
    sad:
      'data:image/png;base64,',
    angry:
      'data:image/png;base64,',
  },
};

export default {
  find: (scope: string, name: string) => {
    return (
      (icons[scope as keyof thing] &&
        icons[scope as keyof thing][name as keyof type2]) ||
      'n/a'
    );
  },
};
